import {
  trigger,
  transition,
  style,
  query,
  animate,
  AnimationMetadata
} from '@angular/animations';

export const routeFadeAnimation: AnimationMetadata = trigger(
  'routeAnimations',
  [
    transition('* => *', [
      query(
        ':enter',
        [
          style({
            opacity: 0
          })
        ],
        {
          optional: true
        }
      ),
      query(
        ':leave',
        [
          style({
            opacity: 1
          }),
          animate('0.3s', style({ opacity: 0 }))
        ],
        { optional: true }
      ),
      query(
        ':enter',
        [style({ opacity: 0 }), animate('0.3s', style({ opacity: 1 }))],
        { optional: true }
      )
    ])
  ]
);
